.nav {
  position: sticky;
  top: 0;
  padding: 1rem;
  box-shadow: 0 0 14px 0 #ccc;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;

  .menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 1rem;
  }

  .chevronDown {
    box-sizing: border-box;
    position: relative;
    display: block;
    transform: scale(var(--ggs, 1));
    width: 20px;
    height: 20px;
    border: 2px solid transparent;
    border-radius: 100px;
    transition: 150ms;

    &::after {
      content: "";
      display: block;
      box-sizing: border-box;
      position: absolute;
      width: 8px;
      height: 8px;
      border-bottom: 2px solid;
      border-right: 2px solid;
      transform: rotate(45deg);
      left: 4px;
      top: 2px;
    }

    &.open {
      transform: rotate(180deg);
    }
  }
}
